<template>
  <div>
    <div
      class="search-form"
     
    >
      <div class="form-item">
        <span>订单类型：</span>
        <el-select
          v-model="pickerIndex"
          placeholder="请选择"
          @change="selectChange"
        >
          <el-option
            v-for="(item, index) in bankList"
            :key="index"
            :label="item"
            :value="index"
          >
          </el-option>
        </el-select>
      </div>

      
    </div>



    <el-table :data="dailiData" style="width: 100%">
      <el-table-column prop="user_name" label="账号" align="center"></el-table-column>
      <el-table-column label="类型" align="center">
          <template v-slot="scope">
              <span v-if="scope.row.user_name">下级代理</span>
          </template>
      </el-table-column>
      <el-table-column prop="orderNum" label="订单数量(单)" align="center"></el-table-column>
      <el-table-column prop="orderProfitLoss" label="订单盈亏(元)" align="center"></el-table-column>
      <el-table-column label="操作" align="center">
          <template v-slot="scope">
               <el-button size="small" type="primary" v-if="scope.row.user_name" @click="seeBaobiao(scope.row.id,scope.row.user_name)">查看报表</el-button>
                <el-button size="small" type="success" @click="nextUser(scope.row.id)">查看下级</el-button>
          </template>

      </el-table-column>

    </el-table>
    <div style="padding: 20px 0; width: 1200px; text-align: center">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="curPage"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="limit"
          layout="total, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
   
  </div>
</template>

<script>
import { lowerLevelReport } from "../api/public";
export default {
  data() {
    return {
      tabCur: 0,
      yhhdList: [],
      curPage: 1,
      total: 1,
      limit: 10,

      curUserId: "",
      curUserName: "",
      pickerIndex: 0,
      bankList: ["今日", "昨日", "本月", "上月"],
      parentId: "",

      nextLength: 0,
      dailiData: [],

      curId: '',
      curName: '',
      inputVal:''
    };
  },


  mounted() {
    this.curId = JSON.parse(localStorage.getItem("user_info")).id;
    this.curUserName = JSON.parse(localStorage.getItem("user_info")).user_name;

    this.discountList();
  },
  methods: {    
      seeBaobiao(id,name) {
          this.$router.push('/agentCenter?id=3&itemId='+id+'&name='+name)
      },
    selectChange(e) {
      console.log(e);
      console.log(this.pickerIndex);
      this.pickerIndex = e
      this.discountList();
    }, 
    tabClick(i) {
      this.tabCur = i;
      this.discountList();
    },
    discountList() {     
      lowerLevelReport({"page": this.curPage, "limit": this.limit, "searchParams": {
        "parent_id": this.curId,
        "dateFlag": this.pickerIndex,
        }}).then((res) => {
        if (res.code == 1) {
          this.dailiData = res.data;
          this.total = res.count
        } else {
            this.total = 0
            this.dailiData = []
        }
      });
    },
    nextUser(id) {
        this.curId = id
        this.discountList()
    },
    submit() {
        this.curUserName = this.inputVal
		this.discountList()
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(this.curPage);
      this.curPage = val
      this.discountList();
      this.getDailiList();
      console.log(`当前页: ${val}`);
    },
    
  },
};
</script>