<template>
  <div>
   



    <el-table :data="dailiData" @row-click="rowClick" style="width: 100%;margin-top:20px;">
      <el-table-column prop="user_name" label="用户名" align="center"></el-table-column>
      <el-table-column prop="childNum" label="下级人数" align="center"></el-table-column>
      <el-table-column label="层级" align="center">
          <template v-slot='scope'>
              {{scope.row.parent_id}}级代理
          </template>
      </el-table-column>
      <el-table-column prop="add_time" label="登录时间" align="center"></el-table-column>
     
          
    </el-table>
     <div style="padding: 20px 0; width: 1200px; text-align: center">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="curPage"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="limit"
          layout="total, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
   
  </div>
</template>

<script>
import { usersDailiGuanli } from "../api/public";
export default {
  data() {
    return {
      tabCur: 0,
      yhhdList: [],
      curPage: 1,
      total: 1,
      limit: 10,

      curUserId: "",
      curUserName: "",
      pickerIndex: 0,
      bankList: ["今日", "昨日", "本月", "上月"],

      typeIndex: 0,
      typeList: ["所有类型", "提现记录", "充值记录"],

      parentId: "",

      nextLength: 0,
      dailiData: [],

      curId: '',
      curName: '',
      inputVal:''
    };
  },


  mounted() {
    this.$nextTick(()=>{
        this.curId = JSON.parse(localStorage.getItem("user_info")).id;
        this.curUserName =JSON.parse(localStorage.getItem("user_info")).user_name;
        this.discountList();

    })
    
  },
  methods: {    
       rowClick(row) {
      this.curPage = 1;
      this.parentId = row.parent_id;
      this.curId = row.id;
      this.curUserName = row.user_name;
      this.discountList();
    //   this.getDailiList();
      console.log(this.parentId);
    },
    selectChange(e) {
      console.log(e);
      console.log(this.pickerIndex);
      this.pickerIndex = e
      this.discountList();
    },
    typeselectChange(e) {
        this.typeIndex = e
        this.discountList();
    }, 
    tabClick(i) {
      this.tabCur = i;
      this.discountList();
    },
    discountList() {      
        usersDailiGuanli({"page": this.curPage, "limit": this.limit, "searchParams": {           
           "parent_id": this.curId   
        }}).then((res) => {
             this.total = res.count
            if (res.code == 1) {
                this.dailiData = res.data;
                
            } else {
                this.dailiData = []
            }
        });
    },
    submit() {
        this.curUserName = this.inputVal
		this.discountList()
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(this.curPage);
      this.curPage = val
      this.discountList();
      this.getDailiList();
      console.log(`当前页: ${val}`);
    },
    
  },
};
</script>