<template>
  <div>
    <div
      class="search-form"
      style="display: flex; justify-content: space-between"
    >
      <div class="form-item">
        <span>订单类型：</span>
        <el-select
          v-model="pickerIndex"
          placeholder="请选择"
          @change="selectChange"
        >
          <el-option
            v-for="(item, index) in bankList"
            :key="index"
            :label="item"
            :value="index"
          >
          </el-option>
        </el-select>
      </div>
    </div>

    <el-table :data="yhhdList" @row-click="rowClick" style="width: 100%">
      <el-table-column label="订单号" align="center" prop="o_order_no">
      </el-table-column>
      <el-table-column label="投入订单金额(元)" align="center" prop="o_amount">
      </el-table-column>
      <el-table-column prop="order_no" label="收益流水编号" align="center">
      </el-table-column>
      <el-table-column prop="amount" label="订单收益金额(元)" align="center">
      </el-table-column>
      <el-table-column prop="add_time" label="收益时间" align="center">
      </el-table-column>
      <el-table-column label="订单归属" align="center">
          <template v-slot="scope">
              <span style="color:red;">{{scope.row.layer_id}}级代理订单</span>
          </template>
      </el-table-column>
    </el-table>
    <div style="padding: 20px 0; width: 1200px; text-align: center">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="curPage"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="limit"
        layout="total,  prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { syList } from "../api/public";
export default {
  data() {
    return {
      tabCur: 0,
      yhhdList: [],
      curPage: 1,
      total: 1,
      limit: 10,

      curUserId: "",
      curUserName: "",
      pickerIndex: 0,
      bankList: ["我的订单", "代理订单"],
      parentId: "",

      nextLength: 0,
      dailiData: {},
    };
  },

  watch: {
    $route: "fetchData",
  },
  mounted() {
    this.curUserId = JSON.parse(localStorage.getItem("user_info")).id;
    this.curUserName = JSON.parse(localStorage.getItem("user_info")).user_name;

    this.discountList();
  },
  methods: {
    rowClick(row) {
      this.curPage = 1;
      this.parentId = row.parent_id;
      this.curUserId = row.id;
      this.curUserName = row.user_name;
      this.discountList();

      console.log(this.parentId);
    },
    selectChange(e) {
      console.log(e);
      console.log(this.pickerIndex);
      this.discountList();
    },
    fetchData(newVal, oldVa) {
      console.log(oldVa); //oldVa 上一次url
      console.log(newVal); //newVal 这一次的url
      this.tabCur = newVal.query.id;
      this.discountList();
    },
    tabClick(i) {
      this.tabCur = i;
      this.discountList();
    },

    discountList() {
      let _this = this;

      syList({
        page: _this.curPage,
        limit: _this.limit,
        searchParams: {
          user_id: this.curUserId,
          show_layer_id: this.pickerIndex,
        },
      }).then((res) => {
        if (res.code == 1) {
          this.yhhdList = res.data;
          this.total = res.count;
        }
      });
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(this.curPage);
      this.curPage = val
      this.discountList();

      console.log(`当前页: ${val}`);
    },
  },
};
</script>