<template>
  <div style="padding: 30px 0">
    <div class="d-tabs">
      <div class="d-tabs-head">
        <div class="tab-nav">
          <div
            class="tab-nav-item"
            :class="{ active: tabCur == 0 }"
            @click="tabClick(0)"
          >
            我的团队
          </div>
          <div
            class="tab-nav-item"
            :class="{ active: tabCur == 1 }"
            @click="tabClick(1)"
          >
            订单收益
          </div>
          <div
            class="tab-nav-item"
            :class="{ active: tabCur == 2 }"
            @click="tabClick(2)"
          >
            我的订单
          </div>
          <div
            class="tab-nav-item"
            :class="{ active: tabCur == 3 }"
            @click="tabClick(3)"
          >
            代理报表
          </div>
          <div
            class="tab-nav-item"
            :class="{ active: tabCur == 4 }"
            @click="tabClick(4)"
          >
            下级报表
          </div>
          <div
            class="tab-nav-item"
            :class="{ active: tabCur == 5 }"
            @click="tabClick(5)"
          >
            账单报表
          </div>
          <div
            class="tab-nav-item"
            :class="{ active: tabCur == 6 }"
            @click="tabClick(6)"
          >
            会员管理
          </div>
          <div
            class="tab-nav-item"
            :class="{ active: tabCur == 7 }"
            @click="tabClick(7)"
          >
            订单明细
          </div>
        </div>
      </div>
      <div class="d-tabs-content">
        <div class="tab-pane" label="充值记录" v-if="tabCur == 0">
          <div
            class="search-form"
            style="display: flex; justify-content: space-between"
          >
            <div class="form-item">
              <span>时间：</span>
              <el-select
                v-model="pickerIndex"
                placeholder="请选择"
                @change="selectChange"
              >
                <el-option
                  v-for="(item, index) in bankList"
                  :key="index"
                  :label="item"
                  :value="index"
                >
                </el-option>
              </el-select>
            </div>

            <div class="form-item">
              <button
                v-if="parentId"
                class="sm-btn clear-btn"
                style="margin-right: 10px"
                @click="goBack"
              >
                <i class="el-icon-refresh-left"></i>返回上一级
              </button>
            </div>
          </div>

          <div
            style="
              display: flex;
              justify-content: space-between;
              padding: 0 20px 20px;
            "
          >
            <div>直属下级人数：{{ nextLength }}人</div>
            <div>新增人数：{{ dailiData.regisCount }}人</div>
            <div>充值人数：{{ dailiData.czCount }}人</div>
          </div>

          <el-table :data="yhhdList" @row-click="rowClick" style="width: 100%">
            <el-table-column label="头像" align="center">
              <template v-slot="scope">
                <img
                  :src="
                    scope.row.head_url || require('../assets/img/defalt_tx.png')
                  "
                  style="width: 40px; height: 40px; border-radius: 50%"
                  alt=""
                />
              </template>
            </el-table-column>
            <el-table-column label="昵称" align="center">
              <template v-slot="scope">
                {{
                  scope.row.nick_name ||
                  scope.row.user_name.substring(0, 3) +
                    new Array(scope.row.user_name.substring(3).length).join("*")
                }}
              </template>
            </el-table-column>
            <el-table-column prop="amountTzSum" label="总投资" align="center">
            </el-table-column>
            <el-table-column prop="amountSySum" label="总收益" align="center">
            </el-table-column>
            <el-table-column prop="add_time" label="收益时间" align="center">
            </el-table-column>
          </el-table>
          <div style="padding: 20px 0; width: 1200px; text-align: center">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="curPage"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="limit"
          layout="total,  prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
        </div>
        <div class="tab-pane" label="订单收益" v-if="tabCur == 1">
          <ddsy />
        </div>

        <div class="tab-pane" label="我的订单" v-if="tabCur == 2">
          <my-order />
        </div>

        <div class="tab-pane" label="代理报表" v-if="tabCur == 3">
          <dlbb />
        </div>
        <div class="tab-pane" label="下级报表" v-if="tabCur == 4">
          <xjbb />
        </div>

        <div class="tab-pane" label="账单报表" v-if="tabCur == 5">
          <zdbb />
        </div>
        <div class="tab-pane" label="会员管理" v-if="tabCur == 6">
          <hygl />
        </div>
        <div class="tab-pane" label="订单明细" v-if="tabCur == 7">
          <ddmx />
        </div>


      </div>

      
    </div>
  </div>
</template>

<script>
import ddsy from "../components/ddsy";
import myOrder from "../components/myOrder";
import dlbb from "../components/dlbb";
import xjbb from "../components/xjbb";
import zdbb from "../components/zdbb";
import hygl from "../components/hygl";
import ddmx from "../components/ddmx";
import { myTeamUserList, dailiDetail } from "../api/public";
export default {
  components: {
      ddsy,
      myOrder,
      dlbb,
      xjbb,
      zdbb,
      hygl,
      ddmx
  },
  data() {
    return {
      tabCur: 0,
      yhhdList: [],
      curPage: 1,
      total: 1,
      limit: 10,

      curUserId: "",
      curUserName: "",
      pickerIndex: 0,
      bankList: ["今日", "昨日", "本月", "上月"],
      parentId: "",

      nextLength: 0,
      dailiData: {},
    };
  },
  watch: {
    $route: "fetchData",
  },
  mounted() {
    this.curUserId = JSON.parse(localStorage.getItem("user_info")).id;
    this.curUserName = JSON.parse(localStorage.getItem("user_info")).user_name;
    this.tabCur = this.$route.query.id || 0;
    this.discountList();
    this.getDailiList();
  },
  methods: {
    goBack() {
      this.curUserId = this.parentId;
      console.log(this.curUserId);
      this.discountList();
      this.getDailiList();
    },
    rowClick(row) {
      this.curPage = 1;
      this.parentId = row.parent_id;
      this.curUserId = row.id;
      this.curUserName = row.user_name;
      this.discountList();
      this.getDailiList();
      console.log(this.parentId);
    },
    selectChange(e) {
      console.log(e);
      console.log(this.pickerIndex);
      this.discountList();
      this.getDailiList();
    },
    fetchData(newVal, oldVa) {
      console.log(oldVa); //oldVa 上一次url
      console.log(newVal); //newVal 这一次的url
      this.tabCur = newVal.query.id;
      this.discountList();
      this.tabCur == 0 && this.getDailiList();
    },
    tabClick(i) {
      this.tabCur = i;
      this.discountList();
      this.tabCur == 0 && this.getDailiList();
    },
    getDailiList() {
      dailiDetail({
        id: this.curUserId,
        dateFlag: this.pickerIndex,
        user_name: this.curUserName,
      }).then((res) => {
        if (res.code == 1) {
          this.dailiData = res.data;
        }
      });
    },
    discountList() {
      let _this = this;
      if (this.tabCur == 0) {
        myTeamUserList({
          page: _this.curPage,
          limit: _this.limit,
          searchParams: {
            parent_id: this.curUserId,
            dateFlag: this.pickerIndex,
          },
        }).then((res) => {
          if (res.code == 1) {
            this.nextLength = res.count;
            this.yhhdList = res.data;
            this.total = res.count;
          }
        });
      } else {
        // txList({"page": _this.curPage, "limit": _this.limit, "searchParams": {
        //     "user_id": JSON.parse(localStorage.getItem("user_info")).id
        // }}).then(res => {
        //     console.log(res)
        //     if (res.code == 1) {
        //         this.yhhdList = res.data
        //         this.total = res.count
        //     }
        // })
      }
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(this.curPage);
      this.curPage = val
      this.discountList();
      this.getDailiList();
      console.log(`当前页: ${val}`);
    },
  },
};
</script>

<style>
.d-tabs {
  width: 1200px;
  min-height: 630px;
  margin: 0 auto;
  background: #fff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12), 0 0 6px 0 rgba(0, 0, 0, 0.04);
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  position: relative;
}

.d-tabs .d-tabs-head {
  overflow: hidden;
  height: 40px;
}

.d-tabs .inline-item {
  background: #282d4f;
  width: 100%;
  font-size: 0;
}

.d-tabs .inline-item .tab-nav-item {
  letter-spacing: 1px;
  display: inline-block;
  padding: 0 10px;
  color: #fff;
  font-size: 14px;
  height: 26px;
  line-height: 26px;
  transition: all 0.3s;
  margin: 7px 10px;
  position: relative;
  border-radius: 4px;
}

.d-tabs .inline-item .tab-nav-item:hover {
  color: #ec002c;
  text-decoration: underline;
  cursor: pointer;
}

.d-tabs .inline-item .active {
  background-image: linear-gradient(-180deg, #d4d4d4, #fff);
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
  background: #fff;
  color: #282d4f;
}

.d-tabs .inline-item .active:hover {
  color: #282d4f;
  text-decoration: none;
}

.d-tabs .tab-nav {
  background: #282d4f;
  width: 100%;
  font-size: 0;
}

.d-tabs .tab-nav .tab-nav-item {
  letter-spacing: 1px;
  display: inline-block;
  padding: 0 32px;
  color: #fff;
  font-size: 14px;
  height: 40px;
  line-height: 40px;
  font-weight: 700;
  transition: all 0.3s;
  position: relative;
}

.d-tabs .tab-nav .tab-nav-item:hover {
  background-color: #7e5acf;
  cursor: pointer;
}

.d-tabs .tab-nav .active {
  background: #fff;
  color: #282d4f;
}

.d-tabs .tab-nav .active:hover {
  background-color: #fff;
  color: #282d4f;
  cursor: inherit;
}

.d-tabs .both-side {
  background: #282d4f;
  width: 100%;
  font-size: 0;
}

.d-tabs .both-side .tab-nav-item {
  letter-spacing: 1px;
  display: inline-block;
  position: relative;
  margin: 0 32px;
  color: #ec002c;
  font-size: 14px;
  height: 40px;
  line-height: 40px;
  font-weight: 700;
}

.d-tabs .both-side .tab-nav-item:before {
  content: "";
  height: 4px;
  width: 4px;
  background: #ec002c;
  position: absolute;
  left: -10px;
  top: 17px;
}

.d-tabs .both-side .tab-nav-item:after {
  content: "";
  height: 4px;
  width: 4px;
  background: #ec002c;
  position: absolute;
  right: -10px;
  top: 17px;
}

.d-tabs .show-tab {
  position: absolute;
  width: 100%;
  top: 0;
  z-index: 1;
}

.d-tabs .show-tab .tav-rate {
  visibility: visible;
  opacity: 1;
}
</style>