<template>
  <div>
    <div
      class="search-form"
     
    >
      <div class="form-item">
        <span>时间：</span>
        <el-select
          v-model="pickerIndex"
          placeholder="请选择"
          @change="selectChange"
        >
          <el-option
            v-for="(item, index) in bankList"
            :key="index"
            :label="item"
            :value="index"
          >
          </el-option>
        </el-select>
      </div>

      <div class="form-item">
          <el-input v-model="inputVal" placeholder="请输入自己或者下级用户名"></el-input>
      </div>
      <div class="form-item">
              <button                
                class="sm-btn clear-btn"
                style="margin-right: 10px"
                @click="submit"
              >
               搜索
              </button>
            </div>
    </div>



    <el-table :data="dailiData" style="width: 100%">
      <el-table-column prop="czAmountCaiJin" label="活动礼金" align="center"></el-table-column>
      <el-table-column prop="firstCz" label="首充人数" align="center"></el-table-column>
      <el-table-column prop="regisCount" label="注册人数" align="center"></el-table-column>
      <el-table-column prop="czCount" label="充值人数" align="center"></el-table-column>
      <el-table-column prop="czAmount" label="充值金额" align="center"></el-table-column>
      <el-table-column prop="txAmount" label="提现金额" align="center"></el-table-column>
      <el-table-column prop="txCount" label="提现人数" align="center"></el-table-column>
      <el-table-column prop="orderCount" label="订单人数" align="center"></el-table-column>
          
    </el-table>
    <el-table :data="dailiData" style="width: 100%">
        <el-table-column prop="orderNum" label="订单数量" align="center"></el-table-column>
        <el-table-column prop="orderAmount" label="订单总金额" align="center"></el-table-column>
        <el-table-column prop="orderNumOver" label="订单完成数量" align="center"></el-table-column>
        <el-table-column prop="orderAmountOver" label="订单完成总金额" align="center"></el-table-column>
        <el-table-column prop="childNum" label="下级人数" align="center"></el-table-column>
        <el-table-column prop="syAmountDl" label="代理佣金" align="center"></el-table-column>
        <el-table-column prop="syAmountGr" label="个人收益" align="center"></el-table-column>
        <el-table-column prop="syAmountTd" label="团队收益" align="center"></el-table-column>
    </el-table>
   
  </div>
</template>

<script>
import { dailiDetail } from "../api/public";
export default {
  data() {
    return {
      tabCur: 0,
      yhhdList: [],
      curPage: 1,
      total: 1,
      limit: 10,

      curUserId: "",
      curUserName: "",
      pickerIndex: 0,
      bankList: ["今日", "昨日", "本月", "上月"],
      parentId: "",

      nextLength: 0,
      dailiData: [],

      curId: '',
      curName: '',
      inputVal:''
    };
  },


  mounted() {
    this.$nextTick(()=>{
        this.curId = this.$route.query.itemId || JSON.parse(localStorage.getItem("user_info")).id;

        this.curUserName = this.$route.query.name || JSON.parse(localStorage.getItem("user_info")).user_name;

        this.discountList();

    })
    
  },
  methods: {
    
    selectChange(e) {
      console.log(e);
      console.log(this.pickerIndex);
      this.pickerIndex = e
      this.discountList();
    },
 
    tabClick(i) {
      this.tabCur = i;
      this.discountList();
    },
    discountList() {     
      dailiDetail({
        "id": +this.curId,
        "dateFlag": this.pickerIndex,
        "user_name": this.curUserName
      }).then((res) => {
        if (res.code == 1) {
          this.dailiData[0] = res.data;
        } else {
            this.dailiData = []
        }
      });
    },
    submit() {
        this.curUserName = this.inputVal
		this.discountList()
    }
    
  },
};
</script>