<template>
  <div>
    <div
      class="search-form"
     
    >
      <div class="form-item">
        <span>时间：</span>
        <el-select
          v-model="pickerIndex"
          placeholder="请选择"
          @change="selectChange"
        >
          <el-option
            v-for="(item, index) in bankList"
            :key="index"
            :label="item"
            :value="index"
          >
          </el-option>
        </el-select>
      </div>
       <div class="form-item">
        <span>类型：</span>
        <el-select
          v-model="typeIndex"
          placeholder="请选择"
          @change="typeselectChange"
        >
          <el-option
            v-for="(item, index) in typeList"
            :key="index"
            :label="item"
            :value="index"
          >
          </el-option>
        </el-select>
      </div>

      <div class="form-item">
          <el-input v-model="inputVal" placeholder="请输入自己或者下级用户名"></el-input>
      </div>
      <div class="form-item">
              <button                
                class="sm-btn clear-btn"
                style="margin-right: 10px"
                @click="submit"
              >
               搜索
              </button>
            </div>
    </div>



    <el-table :data="dailiData" style="width: 100%">
      <el-table-column prop="order_no" label="订单号" align="center"></el-table-column>
      <el-table-column prop="user_name" label="用户名" align="center"></el-table-column>
      <el-table-column prop="amount" label="投入订单金额(元)" align="center"></el-table-column>
      <el-table-column prop="change_amount" label="订单状态" align="center">
          <template v-slot="scope">
              <span v-if="scope.row.status <= 2">{{['已申请订单','冻结订单','结束订单'][scope.row.status]}}</span>
            <span v-else-if="scope.row.status == 3 || scope.row.status == 4">{{['系统撤销订单','自主撤销订单'][scope.row.status-3]}}</span>
            <span v-else>未通过订单</span>
          </template>
      </el-table-column>
      <el-table-column :label="`${['申请','冻结','结束','撤销',''][typeIndex]}时间`" align="center">
          <template v-slot="scope">
              <span v-if="typeIndex==0">{{scope.row.add_time}}</span>
              <span v-if="typeIndex==1">{{scope.row.start_time}}</span>
              <span v-if="typeIndex==2">{{scope.row.end_time}}</span>
              <span v-if="typeIndex==3">{{scope.row.cancel_time}}</span>
              <span v-else>{{scope.row.cancel_time}}</span>
          </template>
      </el-table-column>          
    </el-table>
     <div style="padding: 20px 0; width: 1200px; text-align: center">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="curPage"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="limit"
          layout="total, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
   
  </div>
</template>

<script>
import { userOrderList } from "../api/public";
export default {
  data() {
    return {
      tabCur: 0,
      yhhdList: [],
      curPage: 1,
      total: 1,
      limit: 10,

      curUserId: "",
      curUserName: "",
      pickerIndex: 0,
      bankList: ["今日", "昨日", "本月", "上月"],

      typeIndex: 0,
      typeList: ['已申请', '已冻结', '已结束', '撤销', '未通过'],

      parentId: "",

      nextLength: 0,
      dailiData: [],

      curId: '',
      curName: '',
      inputVal:''
    };
  },


  mounted() {
    this.$nextTick(()=>{
        this.curId = JSON.parse(localStorage.getItem("user_info")).id;

        this.curUserName = ''

        this.discountList();

    })
    
  },
  methods: {
    
    selectChange(e) {
      console.log(e);
      console.log(this.pickerIndex);
      this.pickerIndex = e
      this.discountList();
    },
    typeselectChange(e) {
        this.typeIndex = e
        this.discountList();
    }, 
    // tabClick(i) {
    //   this.tabCur = i;
    //   this.discountList();
    // },
    discountList() {          
        userOrderList({"page": this.curPage, "limit": this.limit, "searchParams": {
            "user_id": this.curUserName?'':this.curId,
            "showStatus": this.typeIndex == 4 ? 5 : this.typeIndex, 
            "user_name": this.curUserName,           
            "dateFlag": this.pickerIndex
        }}).then((res) => {
             this.total = res.count
            if (res.code == 1) {
                this.dailiData = res.data;
                
            } else {
                this.dailiData = []
            }
        });
    },
    submit() {
        this.curUserName = this.inputVal
		this.discountList()
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(this.curPage);
      this.curPage = val
      this.discountList();
      this.getDailiList();
      console.log(`当前页: ${val}`);
    },
    
  },
};
</script>