<template>
  <div>

    <div
      class="search-form"
      style="display: flex; justify-content: space-between"
    >
      <div class="form-item">
        <span>订单类型：</span>
        <el-select
          v-model="pickerIndex"
          placeholder="请选择"
          @change="selectChange"
        >
          <el-option
            v-for="(item, index) in bankList"
            :key="index"
            :label="item"
            :value="index"
          >
          </el-option>
        </el-select>
      </div>

      <div class="form-item">
              <button                
                class="sm-btn clear-btn"
                style="margin-right: 10px"
                @click="createOrder"
              >
                创建订单
              </button>
            </div>

    </div>
<div
            style="
              display: flex;
              justify-content: space-between;
              padding: 0 20px 20px;
            "
          >
            <div>总收益：￥{{userInfo.amountSySum}}元</div>
            <div>今日总收益：{{userInfo.amountSyToday}}元</div>
            <div>今日收益率：{{userInfo.amountSySum > 0 ? (userInfo.amountSyToday / userInfo.amountSySum * 100).toFixed(2) : 0.00}}%</div>
          </div>

    <el-table :data="yhhdList" style="width: 100%">
      <el-table-column label="订单号" align="center" prop="order_no">
      </el-table-column>
      <el-table-column label="资金(元)" align="center" prop="amount">
      </el-table-column>
      <el-table-column label="日收益率" align="center">
        <template v-slot="scope">{{ scope.row.amountSyRate * 100 }}%</template>
      </el-table-column>
      <el-table-column label="状态" align="center">
        <template v-slot="scope">{{
          ["申请中", "收益", "结束", "系统撤销", "用户撤销", "不通过"][
            scope.row.status
          ]
        }}</template>
      </el-table-column>
      <el-table-column prop="add_time" label="申请时间" align="center">
      </el-table-column>
      <el-table-column label="操作" align="center" v-if="pickerIndex==0">
        <template v-slot="scope">
          <a title="撤销"
            style="color: red;    cursor: pointer;"
            v-if="scope.row.status == 0"
            @click="cancel(scope.row.id)"
            >撤销</a
          >
        </template>
      </el-table-column>
    </el-table>
    <div style="padding: 20px 0; width: 1200px; text-align: center">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="curPage"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="limit"
        layout="total,  prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { orderList,getUser } from "../api/public";
export default {
  data() {
    return {
      tabCur: 0,
      yhhdList: [],
      curPage: 1,
      total: 1,
      limit: 10,

      curUserId: "",
      curUserName: "",
      pickerIndex: 0,
      bankList: ["已申请", "收益中","已结束","撤销","未通过"],
      parentId: "",

      nextLength: 0,
      dailiData: {},
      userInfo: {}
    };
  },

  watch: {
    $route: "fetchData",
  },
  mounted() {
    this.curUserId = JSON.parse(localStorage.getItem("user_info")).id;
    this.curUserName = JSON.parse(localStorage.getItem("user_info")).user_name;

    this.getUserInfo()
    this.discountList();
  },
  methods: {
    // 创建订单
    createOrder() {

    },
    getUserInfo() {
				getUser({id: JSON.parse(localStorage.getItem('user_info')).id}).then(res => {
					if (res.code == 1) {
						this.userInfo = res.data
					}
				})
			},
    cancel() {
       this.$confirm('你正在撤销当前订单，请确认是否撤销', '撤销订单提醒', {            
         confirmButtonText: '确定',            
         cancelButtonText: '取消',            
         type: 'warning'          
        }).then(() => {            
          console.log('点击确定的操作(调用接口)')      
        }).catch(() => {            
          console.log('几点取消的提醒')      
        });
      // let _this = this
			// 	uni.showModal({
			// 	    title: '撤销订单提醒',
			// 	    content: '你正在撤销当前订单，请确认是否撤销',
			// 	    success: function (res) {
			// 	        if (res.confirm) {
			// 	            _this.$http.post('/api/operate/chexiaoOrders', {"user_id": uni.getStorageSync('user_info').id, order_id: id}).then(res => {
			// 	            	if (res.code == 1) {
			// 	            		uni.showToast({
			// 	            			title: '撤销成功',
			// 	            			icon: "success"
			// 	            		})
			// 						_this.discountList()
			// 	            	} else {
			// 	            		_this.$queue.showToast(res.msg);
			// 	            	}
			// 	            })
			// 	        } else if (res.cancel) {
			// 	            console.log('用户点击取消');
			// 	        }
			// 	    }
			// 	});	
    },
  
    selectChange(e) {
      console.log(e);
      console.log(this.pickerIndex);
      this.discountList();
    },
    fetchData(newVal, oldVa) {
      console.log(oldVa); //oldVa 上一次url
      console.log(newVal); //newVal 这一次的url
      this.tabCur = newVal.query.id;
      this.discountList();
    },
    tabClick(i) {
      this.tabCur = i;
      this.discountList();
    },

    discountList() {
      let _this = this;
      orderList({
        page: _this.curPage,
        limit: _this.limit,
        searchParams: {
          user_id: this.curUserId,
          showStatus: this.pickerIndex
        //   show_layer_id: this.pickerIndex,
        },
      }).then((res) => {
        if (res.code == 1) {
          this.yhhdList = res.data;
          this.total = res.count;
        }
      });
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(this.curPage);
      this.curPage = val
      this.discountList();

      console.log(`当前页: ${val}`);
    },
  },
};
</script>