<template>
  <div>
    <div
      class="search-form"
     
    >
      <div class="form-item">
        <span>时间：</span>
        <el-select
          v-model="pickerIndex"
          placeholder="请选择"
          @change="selectChange"
        >
          <el-option
            v-for="(item, index) in bankList"
            :key="index"
            :label="item"
            :value="index"
          >
          </el-option>
        </el-select>
      </div>
       <div class="form-item">
        <span>类型：</span>
        <el-select
          v-model="typeIndex"
          placeholder="请选择"
          @change="typeselectChange"
        >
          <el-option
            v-for="(item, index) in typeList"
            :key="index"
            :label="item"
            :value="index"
          >
          </el-option>
        </el-select>
      </div>

      <div class="form-item">
          <el-input v-model="inputVal" placeholder="请输入自己或者下级用户名"></el-input>
      </div>
      <div class="form-item">
              <button                
                class="sm-btn clear-btn"
                style="margin-right: 10px"
                @click="submit"
              >
               搜索
              </button>
            </div>
    </div>



    <el-table :data="dailiData" style="width: 100%">
      <el-table-column prop="user_name" label="用户名" align="center"></el-table-column>
      <el-table-column prop="change_amount" label="账变金额" align="center"></el-table-column>
      <el-table-column label="类型" align="center">
          <template v-slot='scope'>
              {{['充值入款','彩金入款','提现出款','订单生成投入','订单撤销','收益收入','下级佣金'][scope.row.type_id]}}
          </template>
      </el-table-column>
      <el-table-column prop="add_time" label="时间" align="center"></el-table-column>
     
          
    </el-table>
     <div style="padding: 20px 0; width: 1200px; text-align: center">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="curPage"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="limit"
          layout="total, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
   
  </div>
</template>

<script>
import { UsersAmountChangeList } from "../api/public";
export default {
  data() {
    return {
      tabCur: 0,
      yhhdList: [],
      curPage: 1,
      total: 1,
      limit: 10,

      curUserId: "",
      curUserName: "",
      pickerIndex: 0,
      bankList: ["今日", "昨日", "本月", "上月"],

      typeIndex: 0,
      typeList: ["所有类型", "提现记录", "充值记录"],

      parentId: "",

      nextLength: 0,
      dailiData: [],

      curId: '',
      curName: '',
      inputVal:''
    };
  },


  mounted() {
    this.$nextTick(()=>{
        this.curId = JSON.parse(localStorage.getItem("user_info")).id;

        this.curUserName =JSON.parse(localStorage.getItem("user_info")).user_name;

        this.discountList();

    })
    
  },
  methods: {
    
    selectChange(e) {
      console.log(e);
      console.log(this.pickerIndex);
      this.pickerIndex = e
      this.discountList();
    },
    typeselectChange(e) {
        this.typeIndex = e
        this.discountList();
    }, 
    tabClick(i) {
      this.tabCur = i;
      this.discountList();
    },
    discountList() {    
      
        UsersAmountChangeList({"page": this.curPage, "limit": this.limit, "searchParams": {
            "user_name": this.curUserName,
            "user_id": this.curId,
            "type_id": ['',2,0][this.typeIndex], // 充值 0 彩金 1 提现 2 订单生成 3 订单撤销 4 收益收入 5 下级佣金 6
            "dateFlag": this.pickerIndex
        }}).then((res) => {
             this.total = res.count
            if (res.code == 1) {
                this.dailiData = res.data;
                
            } else {
                this.dailiData = []
            }
        });
    },
    submit() {
        this.curUserName = this.inputVal
		this.discountList()
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(this.curPage);
      this.curPage = val
      this.discountList();
      this.getDailiList();
      console.log(`当前页: ${val}`);
    },
    
  },
};
</script>